<template>
    <div class="app-container container">
        <div v-if="!app_ready" class="full-page-loader">
            <div>Loading...</div>
        </div>

        <div v-if="!tron_web_installed" class="alert alert-danger">
            Please install TronLink extension and reload this page
        </div>

        <div v-if="tron_web_installed && !user.logged_in" class="alert alert-warning">
            Please unlock your TronLink wallet!
        </div>

        <div v-if="tron_web_installed && user.logged_in">
            <div class="row">
                <div class="col-lg-6 mb-3">
                    <div class="card">
                        <div class="card-body">
                            <h3>Contract info</h3>
                            <b-spinner v-if="loaders.initial_contract_info" variant="primary" label="Spinning" />
                            <template v-if="!loaders.initial_contract_info">
                                <p>
                                    Contract address:
                                    <a
                                        :href="
                                            `https://${
                                                $appConfig.contract.currentNetwork === 'shasta' ? 'shasta.' : ''
                                            }tronscan.org/#/contract/${contract.address}/code`
                                        "
                                        target="_blank"
                                    >
                                        {{ contract.address }}
                                    </a>
                                </p>
                                <p>
                                    Balance:
                                    <strong>{{
                                        contract.contract_balance
                                            ? `${tron_web.fromSun(contract.contract_balance)} TRX`
                                            : '-'
                                    }}</strong>
                                </p>
                                <template v-if="contract.statistic">
                                    <p>
                                        Invested amount:
                                        <strong>{{
                                            contract.statistic.investedAmount
                                                ? `${tron_web.fromSun(contract.statistic.investedAmount)} TRX`
                                                : '-'
                                        }}</strong>
                                        <br />
                                        ReInvested amount:
                                        <strong>{{
                                            contract.statistic.reInvestedAmount
                                                ? `${tron_web.fromSun(contract.statistic.reInvestedAmount)} TRX`
                                                : '-'
                                        }}</strong>
                                        <br />
                                        Withdrawals amount:
                                        <strong>{{
                                            contract.statistic.withdrawalsAmount
                                                ? `${tron_web.fromSun(contract.statistic.withdrawalsAmount)} TRX`
                                                : '-'
                                        }}</strong>
                                        <br />
                                        Investors count:
                                        <strong>{{
                                            contract.statistic.investorsCount
                                                ? contract.statistic.investorsCount.toString()
                                                : '-'
                                        }}</strong>
                                    </p>
                                </template>
                                <hr />
                                <pre v-if="contract.settings">
Settings:
    rootReferrerCode: {{ contract.settings.rootReferrerCode.toString() }}
    investmentPayInterval: {{ contract.settings.investmentPayInterval.toString() }}
    referrerLevelsCount: {{ contract.settings.referrerLevelsCount.toString() }}
    levelsCount: {{ contract.settings.levelsCount.toString() }}
</pre
                                >
                                <pre v-if="contract.latest_investor_code">
latestInvestorCode: {{ contract.latest_investor_code.toString() }}
</pre
                                >
                                <pre v-if="contract.fee_percent && contract.fee_address">
feePercent: {{ contract.fee_percent.toString() }}
feeAddress: {{ contract.fee_address.toString() }}
</pre
                                >

                                Levels settings
                                <b-btn v-b-toggle.levelsSettings variant="primary" size="sm">Toggle</b-btn>
                                <span v-if="loaders.level_settings">(Loading...)</span>
                                <b-collapse id="levelsSettings" class="mt-2">
                                    <table class="table table-sm">
                                        <tr>
                                            <th class="text-center">Level</th>
                                            <th>Settings</th>
                                        </tr>
                                        <tr v-for="(data, level) in contract.level_settings" v-if="data" :key="level">
                                            <td class="text-center">{{ level + 1 }}</td>
                                            <td style="word-break: break-all;">
                                                investAmountMin:
                                                <strong>{{ tron_web.fromSun(data.investAmountMin) }} TRX</strong>
                                                <br />
                                                investAmountMax:
                                                <strong>{{ tron_web.fromSun(data.investAmountMax) }} TRX</strong>
                                                <br />
                                                maxActiveInvestmentsCount:
                                                <strong>{{ data.maxActiveInvestmentsCount }}</strong>
                                                <br />
                                                investmentInterest:
                                                <strong>{{ data.investmentInterest.toNumber() / 1000 }}%</strong>
                                                <br />
                                                investmentLifetime: <strong>{{ data.investmentLifetime }} sec</strong>
                                                <br />
                                                upgradeInvestedAmount:
                                                <strong>{{ tron_web.fromSun(data.upgradeInvestedAmount) }} TRX</strong>
                                                <br />
                                                upgradeReInvestsCount: <strong>{{ data.upgradeReInvestsCount }}</strong>
                                            </td>
                                        </tr>
                                    </table>
                                </b-collapse>
                            </template>
                        </div>
                    </div>
                </div>

                <div class="col-lg-6 mb-3">
                    <div class="card">
                        <div class="card-body">
                            <h3>My info</h3>
                            <b-spinner v-if="loaders.initial_user_info" variant="primary" label="Spinning" />
                            <template v-if="!loaders.initial_user_info">
                                <p>
                                    Referrer UID:
                                    <strong>{{ user.referrer_uid ? user.referrer_uid : '-' }}</strong>
                                </p>
                                <p>
                                    Address: <strong>{{ user.address }}</strong>
                                    <br />
                                    UID: <strong>{{ user.uid ? user.uid : '-' }}</strong>
                                    <b-badge v-if="user.uid.toString() === '0'" variant="danger" class="ml-1">
                                        Not registered yet. Invest to register
                                    </b-badge>
                                    <br />
                                    Level:
                                    <strong>{{ user.level ? user.level.toNumber() + 1 : '-' }}</strong>
                                </p>
                                <p>
                                    Balance:
                                    <strong>{{ user.balance ? `${tron_web.fromSun(user.balance)} TRX` : '-' }}</strong>
                                    <br />
                                    Available referrer earnings:
                                    <strong>{{
                                        user.available_referrer_earnings_amount
                                            ? `${tron_web.fromSun(user.available_referrer_earnings_amount)} TRX`
                                            : '-'
                                    }}</strong>
                                    <b-btn
                                        variant="info"
                                        size="sm"
                                        class="ml-1"
                                        :disabled="loaders.withdraw"
                                        @click="onWithdrawSubmit"
                                        >Withdraw</b-btn
                                    >
                                    <br />
                                    Investments count:
                                    <strong>{{ user.investments_count ? user.investments_count : '-' }}</strong>
                                    <br />
                                    ReInvestments count:
                                    <strong>{{ user.reInvestments_count ? user.reInvestments_count : '-' }}</strong>
                                    <br />
                                    Invested amount:
                                    <strong>{{
                                        user.invested_amount ? `${tron_web.fromSun(user.invested_amount)} TRX` : '-'
                                    }}</strong>
                                    <br />
                                    ReInvested amount:
                                    <strong>{{
                                        user.reInvested_amount ? `${tron_web.fromSun(user.reInvested_amount)} TRX` : '-'
                                    }}</strong>
                                </p>
                                <table class="table table-sm">
                                    <tr>
                                        <th class="text-center">Level</th>
                                        <th class="text-center">Referrals count</th>
                                        <th class="text-center">Referral earnings amount</th>
                                    </tr>
                                    <tr v-for="(data, level) in user.referral_levels" :key="level">
                                        <td class="text-center">{{ level + 1 }}</td>
                                        <td class="text-center">{{ data.referrals_count }}</td>
                                        <td class="text-center">
                                            {{ tron_web.fromSun(data.referrals_earnings_amount) }} TRX
                                        </td>
                                    </tr>
                                </table>
                            </template>
                        </div>
                    </div>
                </div>
            </div>

            <div
                v-if="!loaders.initial_contract_info && !loaders.initial_user_info"
                class="row mb-3 justify-content-center"
            >
                <div class="col-md-5 col-lg-4">
                    <div class="card p-3">
                        <div class="card-body">
                            <b-form @submit.prevent="onSubmitInvestForm">
                                <b-form-group label="TRX amount">
                                    <b-input type="number" step="any" v-model.number="invest_form.amount" />
                                </b-form-group>
                                <b-form-group label="Referrer code">
                                    <b-input type="number" v-model.number="invest_form.referrer_code" />
                                </b-form-group>

                                <div class="d-flex justify-content-center">
                                    <b-btn type="submit" :disabled="loaders.investing || loaders.invest_checking_tx">
                                        Invest
                                    </b-btn>
                                </div>
                                <p v-if="loaders.invest_checking_tx" class="text-center m-0">
                                    Waiting for tx info...
                                </p>
                            </b-form>

                            <pre v-if="invest_form.error" style="white-space: pre-line">
Error:
{{ invest_form.error }}</pre
                            >
                        </div>
                    </div>
                </div>
            </div>

            <div v-if="!loaders.initial_user_info">
                <b-tabs v-if="user.investments.length" class="my-3" content-class="pt-3">
                    <b-tab title="Active" active>
                        <div class="row">
                            <div
                                v-for="investment in user_investments_active"
                                :key="`investment-${investment.index}`"
                                class="col-md-6 col-lg-4 mb-3"
                            >
                                <Investment :tron_web="tron_web" :contract="contract.instance" :data="investment" />
                            </div>
                        </div>
                        <h4 v-if="!user_investments_active.length">No investments yet</h4>
                    </b-tab>
                    <b-tab title="Expired">
                        <div class="row">
                            <div
                                v-for="investment in user_investments_expired"
                                :key="`investment-${investment.index}`"
                                class="col-md-6 col-lg-4 mb-3"
                            >
                                <Investment :tron_web="tron_web" :contract="contract.instance" :data="investment" />
                            </div>
                        </div>
                        <h4 v-if="!user_investments_expired.length">No investments yet</h4>
                    </b-tab>
                    <b-tab title="History">
                        <div class="row">
                            <div
                                v-for="investment in user_investments_history"
                                :key="`investment-${investment.index}`"
                                class="col-md-6 col-lg-4 mb-3"
                            >
                                <Investment :tron_web="tron_web" :contract="contract.instance" :data="investment" />
                            </div>
                        </div>
                        <h4 v-if="!user_investments_history.length">No investments yet</h4>
                    </b-tab>
                </b-tabs>
            </div>
        </div>
    </div>
</template>

<script>
import Investment from './Investment'

function delay(time) {
    return new Promise(resolve => {
        setTimeout(resolve, time)
    })
}

export default {
    name: 'App',
    components: {
        Investment
    },
    data() {
        return {
            polls: {
                logged_in: {
                    id: null,
                    interval: 100,
                    hook: () => {
                        this.user.logged_in = (this.tron_web ? this.tron_web.ready : false) || false
                        if (!this.user.logged_in) {
                            this.user.address = ''
                        }
                    }
                },
                app_data: {
                    id: null,
                    interval: 5000,
                    hook: () => {
                        this.fetchAllData()
                    }
                }
            },
            app_ready: false,
            tron_web: undefined,
            loaders: {
                initial_contract_info: true,
                initial_user_info: true,
                level_settings: false,
                investing: false,
                invest_checking_tx: false,
                withdraw: false
            },
            listeners: {
                onInvest: undefined,
                onWithdraw: undefined,
                onLevelChange: undefined
            },
            contract: {
                instance: undefined,
                address: '',
                contract_balance: undefined,
                statistic: undefined,
                settings: undefined,
                latest_investor_code: undefined,
                fee_percent: undefined,
                fee_address: undefined,
                level_settings: []
            },
            user: {
                logged_in: false,
                address: undefined,
                uid: undefined,
                level: undefined,
                balance: undefined,
                available_referrer_earnings_amount: undefined,
                investments_count: undefined,
                reInvestments_count: undefined,
                invested_amount: undefined,
                reInvested_amount: undefined,
                referral_levels: [],
                investments: []
            },
            invest_form: {
                amount: 10,
                referrer_code: 0,
                error: ''
            }
        }
    },
    computed: {
        current_network() {
            return this.$appConfig.contract.networks[this.$appConfig.contract.currentNetwork]
        },
        tron_web_installed() {
            return this.tron_web !== undefined
        },
        user_investments() {
            return this.user.investments.slice().reverse()
        },
        user_investments_active() {
            return this.user_investments.filter(investment => !investment.is_expired)
        },
        user_investments_expired() {
            return this.user_investments.filter(investment => investment.is_expired && !investment.is_withdrawn)
        },
        user_investments_history() {
            return this.user_investments.filter(investment => investment.is_expired && investment.is_withdrawn)
        }
    },
    async mounted() {
        window.app = this

        // Await tronWeb load in global scope
        await new Promise(resolve => {
            let tries = 0
            const interval = 100

            const check_interval = setInterval(() => {
                if (tries > 2000 / interval) {
                    clearInterval(check_interval)
                    resolve()
                }

                if ((this.tron_web = window.tronWeb)) {
                    clearInterval(check_interval)
                    resolve()
                }

                tries++
            }, interval)
        })

        this.app_ready = true

        // Run app if tronWeb installed
        if (this.tron_web_installed) {
            // Watch for TronLink lock
            setInterval(this.polls.logged_in.hook, this.polls.logged_in.interval)

            this.user.address = await new Promise(resolve => {
                const check_interval = setInterval(() => {
                    const address = this.tron_web.defaultAddress.base58
                    if (address) {
                        clearInterval(check_interval)
                        resolve(address)
                    }
                }, 200)
            })

            // Set contract address and fetch data
            this.contract.instance = await this.tron_web.contract().at(this.current_network.contractAddress)
            this.contract.address = this.tron_web.address.fromHex(this.contract.instance.address)

            // Fetch all data
            await this.fetchContractData()
            this.loaders.initial_contract_info = false
            await this.fetchContractLevelsSettings()

            await this.fetchUserData()
            this.loaders.initial_user_info = false

            this.resetForms()

            // Watch for TronLink address
            this.tron_web.on('addressChanged', address => {
                if (this.user.address !== address.base58) {
                    this.user.address = address.base58
                    this.resetForms()
                    this.fetchUserData()
                }
            })

            await this.initEvents()

            // Bind fetch data on window focus
            window.addEventListener('focus', this.fetchAllData)
            // Poll data
            setInterval(this.polls.app_data.hook, this.polls.app_data.interval)
        }
    },
    methods: {
        async onSubmitInvestForm() {
            this.loaders.investing = true
            this.invest_form.error = ''
            try {
                const tx_id = await this.contract.instance.invest(this.invest_form.referrer_code).send({
                    callValue: this.tron_web.toSun(this.invest_form.amount),
                    feeLimit: 1e8
                })

                console.log('>> Invest tx_id:', tx_id)

                this.loaders.invest_checking_tx = true

                for (;;) {
                    const info = await this.tron_web.trx.getUnconfirmedTransactionInfo(tx_id)

                    if (Object.keys(info).length) {
                        console.log('>> Invest TX info:', info)

                        if (info.result === 'FAILED') {
                            let error_message = this.tron_web.toUtf8(info.resMessage)

                            if (info.contractResult && info.contractResult.length) {
                                // const decoded = this.tron_web.toUtf8(info.contractResult[0])
                                // error_message += ', ' + decoded.substr(decoded.indexOf(',') + 1)
                                error_message += ', ' + this.tron_web.toUtf8(info.contractResult[0].substr(136))
                            }

                            throw new Error(error_message)
                        }

                        break
                    }

                    // Delay
                    await delay(1000)
                }
            } catch (error) {
                console.error('Invest error:', error)

                if (typeof error === 'string') {
                    this.invest_form.error = error
                } else {
                    this.invest_form.error = error.message
                }

                this.makeToast('danger', 'Invest error', this.invest_form.error)
            }

            this.loaders.investing = false
            this.loaders.invest_checking_tx = false
            this.invest_form.amount = 10
        },
        async onWithdrawSubmit() {
            this.loaders.withdraw = true

            try {
                const tx_id = await this.contract.instance.withdrawReferralEarnings().send()
                console.log('>> Withdraw tx_id:', tx_id)

                for (;;) {
                    const info = await this.tron_web.trx.getUnconfirmedTransactionInfo(tx_id)

                    if (Object.keys(info).length) {
                        console.log('>> Withdraw TX info:', info)

                        if (info.result === 'FAILED') {
                            let error_message = this.tron_web.toUtf8(info.resMessage)

                            if (info.contractResult && info.contractResult.length) {
                                // const decoded = this.tron_web.toUtf8(info.contractResult[0])
                                // error_message += ', ' + decoded.substr(decoded.indexOf(',') + 1)
                                error_message += ', ' + this.tron_web.toUtf8(info.contractResult[0].substr(136))
                            }

                            throw new Error(error_message)
                        }

                        break
                    }

                    // Delay
                    await delay(1000)
                }
            } catch (error) {
                console.error('Withdraw error:', error)
                let error_message = ''

                if (typeof error === 'string') {
                    error_message = error
                } else {
                    error_message = error.message
                }

                this.makeToast('danger', 'Withdraw error', error_message)
            }

            this.loaders.withdraw = false
        },
        resetForms() {
            this.invest_form.amount = 10
            this.invest_form.referrer_code = this.user.referrer_uid.toNumber()
            this.invest_form.error = ''
        },
        async initEvents() {
            if (window.onTest22) window.onTest22.stop()
            window.onTest22 = await this.contract.instance.TestEvent().watch((err, eventData) => {
                console.log('> TestEvent22 triggered:', err, eventData)
                if (err) {
                    return console.error('Error with "TestEvent22" event:', err)
                }
                if (eventData) {
                    console.log('> TestEvent22 eventData:', eventData)
                }
            })

            if (this.listeners.onInvest) this.listeners.onInvest.stop()
            this.listeners.onInvest = await this.contract.instance.InvestEvent().watch((err, eventData) => {
                console.log('> InvestEvent triggered:', err, eventData)
                if (err) {
                    return console.error('Error with "InvestEvent" event:', err)
                }
                if (eventData) {
                    console.log('> InvestEvent eventData:', eventData)
                    // this.fetchAllData()
                    if (this.tron_web.address.fromHex(eventData.result.investor) === this.user.address)
                        this.makeToast('success', 'Invest', 'Successfully invested')
                }
            })

            if (this.listeners.onWithdraw) this.listeners.onWithdraw.stop()
            this.listeners.onWithdraw = await this.contract.instance.WithdrawEvent().watch((err, eventData) => {
                console.log('> WithdrawEvent triggered:', err, eventData)
                if (err) {
                    return console.error('Error with "WithdrawEvent" event:', err)
                }
                if (eventData) {
                    console.log('> WithdrawEvent eventData:', eventData)
                    // this.fetchAllData()
                    if (this.tron_web.address.fromHex(eventData.result.investor) === this.user.address)
                        this.makeToast('success', 'Withdraw', 'Successfully withdraw')
                }
            })

            if (this.listeners.onLevelChange) this.listeners.onLevelChange.stop()
            this.listeners.onLevelChange = await this.contract.instance.LevelChangeEvent().watch((err, eventData) => {
                console.log('> LevelChangeEvent triggered:', err, eventData)
                if (err) {
                    return console.error('Error with "LevelChangeEvent" event:', err)
                }
                if (eventData) {
                    console.log('> LevelChangeEvent eventData:', eventData)
                    // this.fetchAllData()
                    if (this.tron_web.address.fromHex(eventData.result.investor) === this.user.address)
                        this.makeToast('success', 'Level up', 'New level')
                }
            })
        },
        async fetchAllData() {
            // console.log('> fetchAllData called')

            if (!this.user.logged_in) return

            await this.fetchContractData()
            await this.fetchUserData()
        },
        async fetchContractData() {
            // console.log('> fetchContractData called')
            if (!this.user.logged_in) return
            this.contract.contract_balance = await this.contract.instance.contractBalance().call()

            if (!this.user.logged_in) return
            this.contract.statistic = await this.contract.instance.statistic().call()

            if (!this.user.logged_in) return
            this.contract.settings = await this.contract.instance.settings().call()

            if (!this.user.logged_in) return
            this.contract.latest_investor_code = await this.contract.instance.latestInvestorCode().call()

            if (!this.user.logged_in) return
            this.contract.fee_percent = await this.contract.instance.FEE_PERCENT().call()

            if (!this.user.logged_in) return
            this.contract.fee_address = await this.contract.instance
                .feeAddress()
                .call()
                .then(address => this.tron_web.address.fromHex(address))
        },
        async fetchContractLevelsSettings() {
            // console.log('> fetchContractLevelsSettings called')
            if (!this.user.logged_in) return

            this.loaders.level_settings = true
            await delay(500)

            const levels_count = this.contract.settings.levelsCount.toNumber()
            this.$set(this.contract, 'level_settings', new Array(levels_count))
            for (let i = 0; i < levels_count; i++) {
                const data = await this.contract.instance.levelsSettings(i).call()
                this.$set(this.contract.level_settings, i, data)
                await delay(10)
            }
            this.loaders.level_settings = false
        },
        async fetchUserData() {
            // console.log('> fetchUserData called')
            if (!this.user.logged_in) return
            this.user.uid = await this.contract.instance.address2uid(this.user.address).call()

            if (!this.user.logged_in) return
            const investor_info = await this.contract.instance.investorInfoByUid(this.user.uid).call()
            this.user.referrer_uid = investor_info[1]
            this.user.level = investor_info[2]
            this.user.balance = investor_info[3]
            this.user.available_referrer_earnings_amount = investor_info[4]
            this.user.investments_count = investor_info[5]
            this.user.reInvestments_count = investor_info[6]
            this.user.invested_amount = investor_info[7]
            this.user.reInvested_amount = investor_info[8]

            this.user.referral_levels = []
            for (let i = 0; i < this.contract.settings.referrerLevelsCount.toNumber(); i++) {
                this.user.referral_levels.push({
                    referrals_count: investor_info[9][i],
                    referrals_earnings_amount: investor_info[10][i]
                })
            }

            if (!this.user.logged_in) return
            const investments_data = await this.contract.instance.investmentsByUid(this.user.uid).call()
            const investments_count = this.user.investments_count.toNumber()
            this.user.investments = []
            for (let i = 0; i < investments_count; i++) {
                const investment = {
                    index: i,
                    created_at: investments_data[0][i],
                    expire_at: investments_data[0][i],
                    amount: investments_data[1][i],
                    level: investments_data[2][i],
                    is_expired: investments_data[3][i],
                    is_withdrawn: investments_data[4][i],
                    total_dividends: investments_data[5][i],
                    available_dividends: investments_data[6][i]
                }

                const investment_level_setting = this.contract.level_settings[investment.level]
                investment.expire_at = investment.created_at.add(investment_level_setting.investmentLifetime)

                this.user.investments.push(investment)
            }
        },
        makeToast(variant, title, message) {
            this.$bvToast.toast(message, {
                title: title,
                variant: variant,
                solid: true
            })
        }
    },
    beforeDestroy() {
        clearInterval(this.polls.logged_in.id)
        clearInterval(this.polls.app_data.id)
        window.removeEventListener('focus', this.fetchAllData)
    }
}
</script>

<style lang="scss">
html {
    font-size: 14px;
}
body {
    background-color: #daeee1 !important;
}
