<template>
    <div class="card">
        <div class="card-header">
            <h5>
                <b-badge v-if="!data.is_expired" variant="success">active</b-badge>
                <b-badge v-if="data.is_expired" variant="danger">expired</b-badge>
                <template v-if="data.index >= 0">&nbsp;Investment #{{ data.index + 1 }}</template>
            </h5>
        </div>
        <div class="card-body">
            Created at: <strong>{{ moment(data.created_at.toNumber() * 1000).format('DD MMM YYYY HH:mm:ss') }}</strong>
            <br />
            Expire at: <strong>{{ moment(data.expire_at.toNumber() * 1000).format('DD MMM YYYY HH:mm:ss') }}</strong>
            <br />
            Created at level: <strong>{{ data.level.toNumber() + 1 }}</strong>
            <br />
            Amount: <strong>{{ tron_web.fromSun(data.amount) }} TRX</strong>
            <br />
            Total dividends: <strong>{{ tron_web.fromSun(data.total_dividends) }} TRX</strong>
            <br />
            Available dividends: <strong>{{ tron_web.fromSun(data.available_dividends) }} TRX</strong>
            <br />
            <br />
            <div>
                <b-progress
                    v-if="!data.is_expired"
                    :value="closing_progress.current"
                    :max="closing_progress.max"
                    :precision="2"
                    show-progress
                    animated
                />
               <div class="d-flex justify-content-between">
                   <b-btn
                       v-if="data.is_expired && !data.is_withdrawn"
                       variant="info"
                       :disabled="loaders.reinvesting || loaders.reinvest_checking_tx"
                       @click="reinvest"
                   >
                       ReInvest
                   </b-btn>
                   <b-btn
                       v-if="data.is_expired && !data.is_withdrawn"
                       variant="info"
                       :disabled="loaders.withdrawing || loaders.withdraw_checking_tx"
                       @click="withdraw"
                   >
                       Withdraw
                   </b-btn>
               </div>

                <p v-if="loaders.reinvest_checking_tx" class="m-0">Waiting for tx info...</p>
                <pre v-if="reinvest_error" style="white-space: pre-line">
Error:
{{ reinvest_error }}</pre
                >

                <p v-if="loaders.withdraw_checking_tx" class="m-0">Waiting for tx info...</p>
                <pre v-if="withdraw_error" style="white-space: pre-line">
Error:
{{ withdraw_error }}</pre
                >
            </div>
        </div>
    </div>
</template>

<script>
import moment from 'moment'

function delay(time) {
    return new Promise(resolve => {
        setTimeout(resolve, time)
    })
}

export default {
    name: 'Investment',
    props: {
        tron_web: {
            type: Object,
            required: true
        },
        contract: {
            type: Object,
            required: true
        },
        data: {
            type: Object,
            required: true
        }
    },
    data() {
        return {
            tx_id: undefined,
            reinvest_error: '',
            withdraw_error: '',
            loaders: {
                reinvesting: false,
                reinvest_checking_tx: false,
                withdrawing: false,
                withdraw_checking_tx: false,
            }
        }
    },
    computed: {
        closing_progress() {
            const lifetime = this.data.expire_at.sub(this.data.created_at)
            const current = this.data.created_at.sub(this.moment().unix()).abs()

            return {
                current: current.toNumber(),
                max: lifetime.toNumber()
            }
        }
    },
    methods: {
        moment() {
            return moment(...arguments)
        },
        async reinvest() {
            console.log('> reinvest clicked')
            this.loaders.reinvesting = true
            this.reinvest_error = ''

            try {
                this.tx_id = await this.contract.reinvest(this.data.index).send()
                console.log('> ReInvest response:', this.tx_id)

                this.loaders.reinvest_checking_tx = true

                for (;;) {
                    const info = await this.tron_web.trx.getUnconfirmedTransactionInfo(this.tx_id)

                    if (Object.keys(info).length) {
                        console.log('>> Invest TX info:', info)

                        if (info.result === 'FAILED') {
                            let error_message = this.tron_web.toUtf8(info.resMessage)

                            if (info.contractResult && info.contractResult.length) {
                                // const decoded = this.tron_web.toUtf8(info.contractResult[0])
                                // error_message += ', ' + decoded.substr(decoded.indexOf(',') + 1)
                                error_message += ', ' + this.tron_web.toUtf8(info.contractResult[0].substr(136))
                            }

                            throw new Error(error_message)
                        }

                        break
                    }

                    // Delay
                    await delay(1000)
                }
            } catch (error) {
                console.error('ReInvest error:', error)

                if (typeof error === 'string') {
                    this.reinvest_error = error
                } else {
                    this.reinvest_error = error.message
                }

                this.makeToast('danger', 'ReInvest error', this.reinvest_error)
            }

            this.loaders.reinvesting = false
            this.loaders.reinvest_checking_tx = false
        },
        async withdraw() {
            console.log('> withdraw clicked')
            this.loaders.withdrawing = true
            this.withdraw_error = ''

            try {
                this.tx_id = await this.contract.withdraw(this.data.index).send()
                console.log('> Withdraw response:', this.tx_id)

                this.loaders.withdraw_checking_tx = true

                for (;;) {
                    const info = await this.tron_web.trx.getUnconfirmedTransactionInfo(this.tx_id)

                    if (Object.keys(info).length) {
                        console.log('>> Withdraw TX info:', info)

                        if (info.result === 'FAILED') {
                            let error_message = this.tron_web.toUtf8(info.resMessage)

                            if (info.contractResult && info.contractResult.length) {
                                // const decoded = this.tron_web.toUtf8(info.contractResult[0])
                                // error_message += ', ' + decoded.substr(decoded.indexOf(',') + 1)
                                error_message += ', ' + this.tron_web.toUtf8(info.contractResult[0].substr(136))
                            }

                            throw new Error(error_message)
                        }

                        break
                    }

                    // Delay
                    await delay(1000)
                }
            } catch (error) {
                console.error('Withdraw error:', error)

                if (typeof error === 'string') {
                    this.withdraw_error = error
                } else {
                    this.withdraw_error = error.message
                }

                this.makeToast('danger', 'Withdraw error', this.withdraw_error)
            }

            this.loaders.withdrawing = false
            this.loaders.withdraw_checking_tx = false
        },
        makeToast(variant, title, message) {
            this.$bvToast.toast(message, {
                title: title,
                variant: variant,
                solid: true
            })
        }
    }
}
</script>
